html {
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

@font-face {
    font-family: 'Alfa Slab One';
    src: url('/fonts/alfaslabone-regular.eot');
    src: url('/fonts/alfaslabone-regular.eot?#iefix') format('embedded-opentype'),
         url('/fonts/alfaslabone-regular.woff2') format('woff2'),
         url('/fonts/alfaslabone-regular.woff') format('woff'),
         url('/fonts/alfaslabone-regular.ttf') format('truetype'),
         url('/fonts/alfaslabone-regular.svg#alfa_slab_oneregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Source Sans Pro Light';
    src: url('/fonts/sourcesanspro-light.eot');
    src: url('/fonts/sourcesanspro-light.eot?#iefix') format('embedded-opentype'),
         url('/fonts/sourcesanspro-light.woff2') format('woff2'),
         url('/fonts/sourcesanspro-light.woff') format('woff'),
         url('/fonts/sourcesanspro-light.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Source Sans Pro Black';
    src: url('/fonts/sourcesanspro-black.eot');
    src: url('/fonts/sourcesanspro-black.eot?#iefix') format('embedded-opentype'),
         url('/fonts/sourcesanspro-black.woff2') format('woff2'),
         url('/fonts/sourcesanspro-black.woff') format('woff'),
         url('/fonts/sourcesanspro-black.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

body {
    margin: 0 auto;
    width: 64em; /* 1024px / 16px */
}

h1 {
    font-family: 'Alfa Slab One', serif;
    font-size: 37px;
    font-weight: normal;
    text-transform: uppercase;
    padding-top: 1em;
    margin-bottom: 0;
}

header {
    border-top: 8px solid black;
    background-color: white;

}
header > p {
    font-family: 'Alfa Slab One';
    font-size: 18px;
    margin: 0 auto;
    text-align: center;
    text-transform: uppercase;
}
header .blah {
    font-family: "Source Sans Pro Black";
    font-size: 32px;
}

header em {
    font-family: "Source Sans Pro Black";
    font-size: 32px;
    font-style: normal;
    color: #003cc3;
}

section {
    color: white;
    width: 1024px;
    height: 258px;
    text-align: center;
}

section h1,
section h2 {
    text-align: center;
}
section h2 {
    font-family: 'Source Sans Pro Black', 'Helvetica Neue', sans-serif;
    font-size: 25px;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 0;
}

section p {
    font-family: 'Source Sans Pro Light', 'Helvetica Neue', sans-serif;
    font-size: 25px;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 1em;
    margin: 0 auto;
    width: 70%;
    text-align: center;
}

.moving {
    background-image: url('../img/moving-1x.jpg');

    background-image: -webkit-image-set( url('../img/moving-1x.jpg') 1x,
                                         url('../img/moving-2x.jpg') 2x );
    background-repeat: no-repeat;
    margin-top: 0;
    margin-bottom: 0;
}

.cleaning {
	background-image: url(../img/cleaning-1x.jpg);

    background-image: -webkit-image-set( url('../img/cleaning-1x.jpg') 1x,
                                         url('../img/cleaning-2x.jpg') 2x );
    background-repeat: no-repeat;
}

.catering {
	background-image: url(../img/catering-1x.jpg);

    background-image: -webkit-image-set( url('../img/catering-1x.jpg') 1x,
                                         url('../img/catering-2x.jpg') 2x );
    background-repeat: no-repeat;
}

.utility {
	background-image: url(../img/utility-1x.jpg);

    background-image: -webkit-image-set( url('../img/utility-1x.jpg') 1x,
                                         url('../img/utility-2x.jpg') 2x );
    background-repeat: no-repeat;
}

button {
    color: black;
    font-family: 'Source Sans Pro Black', sans-serif;
    text-transform: uppercase;
    border-radius: 20px;
    border: 0;
    background-color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


footer {
    text-transform: uppercase;
}

